#big {
    width: 70px;
}

.key {
    width: 38px;
    height: 45px;
    margin: 3px;
    border-radius: 4px;
    display: grid;
    place-items: center;
    font-size: 13px;
    background-color: grey;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    cursor: pointer;
}
